interface AssetStore {
	assetsâµ§all: Set<Immutable<Thing>>
	assetsâµ§recents: Array<Immutable<Thing>>
}

const STORE: AssetStore = {
	assetsâµ§all: new Set<Immutable<Thing>>(),
	assetsâµ§recents: [],
}

/////////////////////////////////////////////////


// "fire and forget"
// called by the asset file itself on load,
// whether itâ€™s used or not
function registerê“½asset_usageâµ§load(asset: Immutable<Thing>): void {
	STORE.assetsâµ§all.add(asset)
}

// "fire and forget"
// called by the user of the asset
// so that we can show "currently/recently" used assets
function registerê“½asset_usageâµ§start(asset: Immutable<Thing>): void {
	if (STORE.assetsâµ§recents.includes(asset))
		return

	STORE.assetsâµ§all.add(asset)
	STORE.assetsâµ§recents.unshift(asset)
	STORE.assetsâµ§recents = STORE.assetsâµ§recents.slice(0, 12)
}

// "fire and forget"
// call is optional
function registerê“½asset_usageâµ§end(asset: Immutable<Thing>): void {
	// useful? TODO if useful
}

// return all currently registered assets
function getê“½assetsâµ§all(): Immutable<Set<Immutable<Thing>>> {
	return STORE.assetsâµ§all
}

// return the most recently used/loaded assets
function getê“½assetsâµ§recents(n = 12): Immutable<Array<Immutable<Thing>>> {
	return STORE.assetsâµ§recents
}

